import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Menu, X } from "lucide-react";
import { ThemeToggle } from "@/components/ThemeToggle";

interface NavigationProps {
  currentSection: string;
  onSectionChange: (section: string) => void;
}

export default function Navigation({ currentSection, onSectionChange }: NavigationProps) {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const sections = [
    { id: "home", label: "Home" },
    { id: "pika", label: "PIKA" },
    { id: "tec", label: "TEC" },
    { id: "about", label: "About Us" },
    { id: "contact", label: "Contact" }
  ];

  const handleNavClick = (sectionId: string) => {
    onSectionChange(sectionId);
    setIsMobileMenuOpen(false);
    console.log(`Navigate to ${sectionId} section`);
  };

  return (
    <nav className="fixed top-4 left-1/2 -translate-x-1/2 z-50 w-[95%] max-w-5xl">
      {/* Glassmorphic Pill Container */}
      <div className="bg-background/60 backdrop-blur-xl border border-border/50 rounded-full shadow-2xl px-6 py-3">
        <div className="flex justify-between items-center">
          {/* Logo */}
          <div className="flex-shrink-0">
            <h1 className="text-xl font-bold text-white">
              PICABORD
            </h1>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-1">
            {sections.map((section) => (
              <Button
                key={section.id}
                variant="ghost"
                size="sm"
                data-testid={`nav-${section.id}`}
                onClick={() => handleNavClick(section.id)}
                className={`relative px-4 py-2 text-sm font-medium transition-all duration-300 rounded-full ${
                  currentSection === section.id
                    ? "text-primary-foreground bg-primary hover:bg-primary/90"
                    : "text-muted-foreground hover:text-foreground hover:bg-accent/50"
                }`}
              >
                {section.label}
              </Button>
            ))}
          </div>

          {/* Theme Toggle & Mobile Menu Button */}
          <div className="flex items-center gap-2">
            <ThemeToggle />
            <div className="md:hidden">
              <Button
                variant="ghost"
                size="icon"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                data-testid="button-mobile-menu"
                className="rounded-full"
              >
                {isMobileMenuOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Navigation Dropdown */}
      {isMobileMenuOpen && (
        <div className="md:hidden mt-2">
          <div className="bg-background/80 backdrop-blur-xl border border-border/50 rounded-3xl shadow-2xl px-4 py-3 space-y-1">
            {sections.map((section) => (
              <Button
                key={section.id}
                variant="ghost"
                size="sm"
                data-testid={`nav-mobile-${section.id}`}
                onClick={() => handleNavClick(section.id)}
                className={`w-full justify-start px-4 py-2.5 text-sm font-medium rounded-full transition-all ${
                  currentSection === section.id
                    ? "text-primary-foreground bg-primary hover:bg-primary/90"
                    : "text-muted-foreground hover:bg-accent/50"
                }`}
              >
                {section.label}
              </Button>
            ))}
          </div>
        </div>
      )}
    </nav>
  );
}